import { useEffect,useRef } from "react";

const useSticky = (customTop='0',refName=null)=>{
  const internalRef = useRef(null);
  const ref = refName || internalRef;
  useEffect(()=>{
    const handleScroll=()=>{      
      if(ref.current){
        const stickyPoint = window.scrollY + ref.current.getBoundingClientRect().top;
        const isSticky=window.scrollY>stickyPoint;
        ref.current.style.position= isSticky ? 'fixed':'absolute';
        ref.current.style.top = isSticky? customTop: 'initial';
      }      
    };        
    window.addEventListener('scroll',handleScroll);
    return()=>window.removeEventListener('scroll',handleScroll);
  },[customTop,ref]);   
  return ref;

};

export default useSticky;